@import url(https://fonts.googleapis.com/css2?family=Bungee&display=swap);
html, body, #root {
  min-height: 100vmin;
  min-width: 100%;
  position: relative;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --board-section-size: 40px;
  --board-background-color: #24135F;
  --board-background-color-wood-secondary: #261a0d;
  --board-background-url: url(https://i.dlpng.com/static/png/1927438-pitch-soccer-field-background-green-football-field-background-image-green-grass-football-in-grass-png-390_260_preview.webp);
  --board-background-url-wood: url("https://previews.123rf.com/images/prapann/prapann1606/prapann160600110/58202559-old-wood-vintage-wood-wall-texture-wood-background-old-panels.jpg");
  --board-background-color-secondary: #9A7611;

}

.BoardContainer {
  width: 120vmin;
  height: 60vmin;
  border: 2.0vmax solid transparent;
  border-radius: 1.0vmax;
  background-color: #24135F;
  background-color: var(--board-background-color);
  box-shadow: 0 12px 10px #9A7611;
  box-shadow: 0 12px 10px var(--board-background-color-secondary);
  position: absolute;
}

.Board {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.Board-Row1 {
  flex: 1 1;
  display: flex;
  flex-direction: row-reverse;
  border-top: 1px dashed white;
  border-right: 1px dashed white;
  border-left: 1px dashed white;
  border-bottom: 1px dashed white;
}

.Board-Row2 {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  border-bottom: 1px dashed white;
  border-right: 1px dashed white;
  border-left: 1px dashed white;
}

.Board-Col1, .Board-Col2 {
  flex: 1 0 6%;
  border: 1px dashed antiquewhite;
  box-shadow: 0 2px 5px #9A7611;
  box-shadow: 0 2px 5px var(--board-background-color-secondary);
}

.BoardSpace {
  flex: 1 0 6%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  position: relative;
}


.BoardSpace:nth-child(2n) {
  background-color: #9A7611;
  background-color: var(--board-background-color-secondary);
}

.BoardSpace.arrow-up {
  justify-content: flex-end;
  -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
          clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.BoardSpace.arrow-down {
  justify-content: flex-start;
  -webkit-clip-path: polygon(100% 0%, 0% 0%, 50% 100%);
          clip-path: polygon(100% 0%, 0% 0%, 50% 100%)
}

.GameButton {
  position: absolute;
  margin: auto;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  background: transparent;
  border: 0;
}

.GameButton img {
  width: 20vmin;
  transition: 0.25s ease-in all;
}

.GameButton:focus {
  outline: #261a0d;
}

.GameButton:hover img {
  transform: scale(1.25);
}

.Piece {
  position: relative;
  display: block;
  height: 4.1vmin;
  width: 4.1vmin;
  margin: -5px;
  border: 2px solid saddlebrown;
  background-color: antiquewhite;
  border-radius: 100px;
  overflow: visible;
}

.Piece-1 {
  border: 2px solid saddlebrown;
  background-color: antiquewhite;
}


.Piece-2 {
  border: 2px solid #24135F;
  background-color: rebeccapurple;
}


.App {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  min-height: 100vmin;
}

.birthday {
  flex: 1 1;
  margin-top: 5vmin;
  margin-bottom: auto;
  font-size: 5vmin;
  font-family: Bungee;
  text-decoration: none;
  transition: all 0.5s;
}

.birthday {
  color: rebeccapurple;
  text-shadow: 5px 5px rgba(0,0,0,0.1);
}

.birthday:hover {
  color: #FF1177;
}

#balloon-container {
  height: 100vh;
  padding: 1em;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
}

.balloon {
  height: 125px;
  width: 105px;
  border-radius: 75% 75% 70% 70%;
  position: relative;
}

.balloon:before {
  content: "";
  height: 75px;
  width: 1px;
  padding: 1px;
  background-color: #FDFD96;
  display: block;
  position: absolute;
  top: 125px;
  left: 0;
  right: 0;
  margin: auto;
}

.balloon:after {
  content: "▲";
  text-align: center;
  display: block;
  position: absolute;
  color: inherit;
  top: 120px;
  left: 0;
  right: 0;
  margin: auto;
}

@-webkit-keyframes float {
  from {transform: translateY(100vh);
    opacity: 1;}
  to {transform: translateY(-300vh);
    opacity: 0;}
}

@keyframes float {
  from {transform: translateY(100vh);
    opacity: 1;}
  to {transform: translateY(-300vh);
    opacity: 0;}
}


#balloon-container {
  position: fixed;
  z-index: 1000;
}




