@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');

.App {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  min-height: 100vmin;
}

.birthday {
  flex: 1;
  margin-top: 5vmin;
  margin-bottom: auto;
  font-size: 5vmin;
  font-family: Bungee;
  text-decoration: none;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}

.birthday {
  color: rebeccapurple;
  text-shadow: 5px 5px rgba(0,0,0,0.1);
}

.birthday:hover {
  color: #FF1177;
}

#balloon-container {
  height: 100vh;
  padding: 1em;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
}

.balloon {
  height: 125px;
  width: 105px;
  border-radius: 75% 75% 70% 70%;
  position: relative;
}

.balloon:before {
  content: "";
  height: 75px;
  width: 1px;
  padding: 1px;
  background-color: #FDFD96;
  display: block;
  position: absolute;
  top: 125px;
  left: 0;
  right: 0;
  margin: auto;
}

.balloon:after {
  content: "▲";
  text-align: center;
  display: block;
  position: absolute;
  color: inherit;
  top: 120px;
  left: 0;
  right: 0;
  margin: auto;
}

@keyframes float {
  from {transform: translateY(100vh);
    opacity: 1;}
  to {transform: translateY(-300vh);
    opacity: 0;}
}


#balloon-container {
  position: fixed;
  z-index: 1000;
}



