:root {
  --board-section-size: 40px;
  --board-background-color: #24135F;
  --board-background-color-wood-secondary: #261a0d;
  --board-background-url: url(https://i.dlpng.com/static/png/1927438-pitch-soccer-field-background-green-football-field-background-image-green-grass-football-in-grass-png-390_260_preview.webp);
  --board-background-url-wood: url("https://previews.123rf.com/images/prapann/prapann1606/prapann160600110/58202559-old-wood-vintage-wood-wall-texture-wood-background-old-panels.jpg");
  --board-background-color-secondary: #9A7611;

}

.BoardContainer {
  width: 120vmin;
  height: 60vmin;
  border: 2.0vmax solid transparent;
  border-radius: 1.0vmax;
  background-color: var(--board-background-color);
  box-shadow: 0 12px 10px var(--board-background-color-secondary);
  position: absolute;
}

.Board {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.Board-Row1 {
  flex: 1;
  display: flex;
  flex-direction: row-reverse;
  border-top: 1px dashed white;
  border-right: 1px dashed white;
  border-left: 1px dashed white;
  border-bottom: 1px dashed white;
}

.Board-Row2 {
  flex: 1;
  display: flex;
  flex-direction: row;
  border-bottom: 1px dashed white;
  border-right: 1px dashed white;
  border-left: 1px dashed white;
}

.Board-Col1, .Board-Col2 {
  flex: 1 0 6%;
  border: 1px dashed antiquewhite;
  box-shadow: 0 2px 5px var(--board-background-color-secondary);
}

.BoardSpace {
  flex: 1 0 6%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  position: relative;
}


.BoardSpace:nth-child(2n) {
  background-color: var(--board-background-color-secondary);
}

.BoardSpace.arrow-up {
  justify-content: flex-end;
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

.BoardSpace.arrow-down {
  justify-content: flex-start;
  clip-path: polygon(100% 0%, 0% 0%, 50% 100%)
}

.GameButton {
  position: absolute;
  margin: auto;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  background: transparent;
  border: 0;
}

.GameButton img {
  width: 20vmin;
  transition: 0.25s ease-in all;
}

.GameButton:focus {
  outline: #261a0d;
}

.GameButton:hover img {
  transform: scale(1.25);
}

.Piece {
  position: relative;
  display: block;
  height: 4.1vmin;
  width: 4.1vmin;
  margin: -5px;
  border: 2px solid saddlebrown;
  background-color: antiquewhite;
  border-radius: 100px;
  overflow: visible;
}

.Piece-1 {
  border: 2px solid saddlebrown;
  background-color: antiquewhite;
}


.Piece-2 {
  border: 2px solid #24135F;
  background-color: rebeccapurple;
}

